/* stylelint-disable */
@use 'sass:math';
/* stylelint-endble */

.embed-responsive {
  display: block;
  position: relative;
  width: 100%;
  overflow: hidden;
  padding: 0;
  margin-top: $typo-margin-vertical;
  margin-bottom: $typo-margin-vertical;

  &::before {
    content: '';
    display: block;
    padding-top: percentage(math.div(9, 16)); //       16/9
  }

  // embed,
  // object,
  iframe,
  video {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
  }

  &--4-3 {
    //     4/3

    &::before {
      padding-top: percentage(3 * 0.25);
    }
  }
}
