//        - or,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.or {
  display: flex;
  align-items: center;

  &:before,
  &:after {
    content: '';
    flex-grow: 1;
    height: 1px;
    background-color: $gray-light;
  }

  &__inner {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    font-size: $font-size-sm;
    color: $gray-light;
  }
}
